import api from '../../common/api.js';
import util from '../../common/util.js';
export default {
	data() {
		return {
			nameTitle: "请输入手机号",
			passTitle: '请输入密码',
            buttonTitle: '登录',
            username: "",
            userpass: "",
            sendTitle:"获取验证码",
            isSend: false,
            // username: "bnshequ",
            // userpass: "bn##123",
            load:false,
            isActive:false,
            tips:'',
            login:false,
            
            from:'',
            testCode:''
		}
	},
	onLoad(op) {
        this.from = op.from
        this.testCode = op.testCode
        var sessionKey = uni.getStorageSync('sessionKey');
        if(sessionKey.length>10){
            uni.switchTab({
                url: '../statistics/statistics'
            });
        }
        this.load = true
        
        
	},
	methods: {
       
        watchNum: function() {
            if (this.userpass.length > 0 && this.username.length>0) {
                this.isActive = true
            }else{
                this.isActive = false
            }
        },
        clickLogin:function(){
            if (this.username == '') {
                
                this.tips = '请输入手机号码'
                return;
            }
            console.log(!util.verifyMobile(this.username));
            if (!util.verifyMobile(this.username)) {
               
                this.tips = '请输入正确的手机号码'
                return;
            }
            if (this.userpass == '') {
             
                this.tips = '请输入验证码'
                return;
            }
            if (this.userpass.length < 6) {
                
                this.tips = '验证码不正确'
                   
                return;
            }
            if(this.from == 'phone'){
                if (this.userpass != '123456' || this.username != '13866668888') {
                    uni.showLoading({
                        title: '加载中' // 数据请求前loading
                    })
                    setTimeout(function() {
                        uni.hideLoading()
                        uni.showToast({
                            title: '账号或密码不正确',
                            icon: "none",
                            duration: 2000
                        })
                            
                    }, 3000)
                            
                    return;
                }
                            
                api.getTempAccount({
                    data: {
                        code: this.testCode
                    }
                }).then(userRes => {
                    console.log(userRes)
                    if (userRes.status == 'OK') {
                        if (userRes.data.errcode == '0') {
                            uni.setStorageSync('sessionId', userRes.data.sessionId)
                            const userObj = {
                                userId: userRes.data.data.id,
                            }
                            api.findUserById({
                                data:userObj
                            }).then(res => {
                                console.log(res)
                                if(res.status == 'OK'){
                                    uni.setStorageSync('userinfo', res.data)
                                    if( res.data && res.data.followCommunityId && res.data.followCommunityId.length > 3 ){
                                        this.emchatLogin(userRes.data.data.id)
                                        
                                        uni.reLaunch({
                                            url:'index?communityid='+res.data.followCommunityId
                                        })
                                        // var pages = getCurrentPages();
                                        // var prevPage = pages[pages.length - 2];
                                        // prevPage.searchCommunityid = res.data.followCommunityId
                                    }else{      //跳转加入服务站
                                        uni.navigateTo({
                                        				  url: '/pages/serverStation/main'
                                        })
                                    }
                                }
                            })
                        }
                    }
                })
            }else{
                if (this.userid == null || this.userid == '' || this.userid == 'null') {
             
                    var unionid = uni.getStorageSync('unionid');
                    var obj = {
                        'verifycode': this.userpass,
                        'phone': this.username,
                        'unionid':unionid
                    }
                    api.createUser({
                        data: obj
                    }).then(userRes => {
                        console.log('userRes:')
                        console.log(userRes)
                        if (userRes.status == "OK") {
                            uni.showToast({
                                title: '绑定成功',
                                icon: "success",
                                duration: 2000
                            })
                        }else{
                            this.tips = userRes.message
                            return
                        }
                        const userObj = {
                            userId: userRes.data.id,
                        }
                        api.findUserById({
                            data:userObj
                        }).then(res => {
                            console.log(userRes)
                            if(res.status == 'OK'){
                                uni.setStorageSync('userinfo', res.data)
                                if( res.data && res.data.followCommunityId && res.data.followCommunityId.length > 3 ){
                                    this.emchatLogin(userRes.data.id)
                                    
                                    uni.reLaunch({
                                        url:'index?communityid='+res.data.followCommunityId
                                    })
                                    // var pages = getCurrentPages();
                                    // var prevPage = pages[pages.length - 2];
                                    // prevPage.searchCommunityid = res.data.followCommunityId
                                }else{      //跳转加入服务站
                                    uni.navigateTo({
                                    				  url: '/pages/serverStation/main'
                                    })
                                }
                            }
                        })
                    })
                }
            }
            
        },emchatLogin:function(userid){
            
            var pwd = userid.substring(1,userid.length-1)
            this.$conn.open({
            	apiUrl: this.$im.config.apiURL,
            	user: userid,
            	pwd: pwd,
            	grant_type: "password",
            	appKey: this.$im.config.appkey
            });
        },
        clickGetCode:function(){
            if (this.username == '') {
                if(!this.loginType){
                    
                    this.tips = '请输入手机号码'
                }else{
                    uni.showToast({
                        title: '请输入手机号码',
                        icon: "none",
                        duration: 2000
                    })
                    this.tips = '请输入手机号码'
                }
                return;
            }
            if(this.isSend){
                return
            }
            this.isSend = true
            var _self = this
            var data = {
                'phone': this.username,
                'type':'4'
            }
            api.getMsgCode({
                data: data
            }).then(res => {
                console.log(res)
                
                var time = 60;
                if (res.status == "OK") {
                    var set = setInterval(function() {
                        _self.sendTitle = --time + 's'
                    }, 1000);
                    setTimeout(function() {
                        _self.sendTitle = "获取验证码"
                        _self.isSend = false
                        clearInterval(set);
                    }, 60000);
                    
                    uni.showToast({
                        title: '发送成功',
                        icon: "success",
                        duration: 2000
                    })
                }else{
                    _self.sendTitle = "获取验证码"
                    _self.isSend = false
                }
            })
        }
	}
}
